.cooperation {
	margin-top: 220rem;

	@media (max-width: 991px) {
		margin-top: 70rem;
	}

	&__content {
		@media (min-width: 992px) {
			display: flex;
			max-width: 1150rem;
			margin: auto;
		}
	}

	&__left {
		@media (min-width: 992px) {
			flex: 0 0 48%;
		}

		@media (max-width: 991px) {
			margin-bottom: 40rem;
		}
	}

	&__title {
		font-size: 36rem;
		line-height: 133%;
		color: #151515;
	}

	&__items {
		display: flex;
		flex-wrap: wrap;
		margin-top: 50rem;
	}

	&__item {
		flex: 0 0 50%;
		padding-bottom: 40rem;
		padding-right: 60rem;

		@media (max-width: 991px) {
			flex: 0 0 47%;
		}

		&--1 {
			.cooperation__num {
				color: #17CDC9;
				border: 1px solid #17CDC9;
			}

			.cooperation__subtitle {
				color: #17CDC9;
			}
		}

		&--2 {
			.cooperation__num {
				color: #FFBD07;
				border: 1px solid #FFBD07;
			}

			.cooperation__subtitle {
				color: #FFBD07;
			}
		}

		&--3 {
			.cooperation__num {
				color: #EE2B62;
				border: 1px solid #EE2B62;
			}

			.cooperation__subtitle {
				color: #EE2B62;
			}
		}

		&--4 {
			.cooperation__num {
				color: #074979;
				border: 1px solid #074979;
			}

			.cooperation__subtitle {
				color: #074979;
			}
		}
	}

	&__num {
		font-size: 27rem;
		line-height: 134.09%;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 45rem;
		height: 45rem;
		border-radius: 50%;
	}

	&__subtitle {
		font-weight: 500;
		font-size: 21rem;
		line-height: 143%;
		margin-top: 20rem;
	}

	&__desc {
		font-size: 13rem;
		line-height: 150%;
		color: #151515;
		margin-top: 10rem;
	}

	&__form {
		.popup__content {
			transform: none;
		}
	}
}