.tabs {
	position: relative;

	&__list {
		list-style-type: none;
		padding: 0;
		max-width: 675rem;
		margin: auto;
		background: #F5F6FA;
		border-radius: 200rem;
		padding: 10rem;

		@media (min-width: 992px) {
			display: flex;
			align-items: center;
		}

		@media (max-width: 991px) {
			border-radius: 10rem;
		}
	}

	&__item:not(:last-child) {
		@media (min-width: 992px) {
			margin-right: 20rem;
		}

		@media (max-width: 991px){
			margin-bottom: 10rem;
		}
	}

	&__btn {
		position: relative;
		padding: 8rem 33rem 8rem 65rem;
		border: none;
		font-weight: 500;
		font-size: 20rem;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
		border-radius: 200rem;

		@media (max-width: 991px) {
			width: 100%;
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 30rem;
			transform: translateY(-50%);
			width: 27rem;
			height: 27rem;
		}

		&--bestsellers {
			color: #FF310C;

			&::before {
				background-image: url('../img/svg/fire.svg');
			}
		}

		&--girls {
			color: #EA5C84;

			&::before {
				background-image: url('../img/svg/girls.svg');
			}
		}

		&--boys {
			color: #2B48D3;

			&::before {
				background-image: url('../img/svg/boys.svg');
			}
		}
	}

	&__btn:hover {
		background-color: #fff;
	}

	&__btn--active {
		background: #fff;
		box-shadow: 0px 6px 12px -6px rgba(24, 39, 75, 0.12),
			0px 8px 24px -4px rgba(24, 39, 75, 0.08);
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
		pointer-events: none;
	}

	&__content {
		display: none;
		background-color: #fff;
		margin-top: 55rem;

		@media (max-width: 991px){
			margin-top: 35rem;
		}
	}

	&__content--active {
		display: block;
	}

}