.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.8);
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	overflow-y: auto;
	overflow-x: hidden;
	transition: all 0.3s ease 0s;
	z-index: 10;

	&.open {
		opacity: 1;
		visibility: visible;
	}

	&.open .popup__content {
		transform: none;
	}

	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3 ease 0s;
	}

	&__content {
		background-color: #fff;
		box-shadow: 0px 8px 18px -6px rgba(24, 39, 75, 0.12),
			0px 12px 42px -4px rgba(24, 39, 75, 0.12);
		border-radius: 20rem;
		border-top: 15px solid #2EBCBA;
		max-width: 500rem;
		padding: 60rem 50rem;
		position: relative;
		transition: transform .3s ease-out;
		transform: translate(0, -70rem);

		@media (max-width: 991px) {
			margin: 0 15rem;
		}
	}

	&__close {
		position: absolute;
		right: 20rem;
		top: 15rem;
		z-index: 3;

		img {
			width: 30rem;
			height: 30rem;
		}
	}

	&__title {
		font-weight: 600;
		font-size: 25rem;
		line-height: 143%;
		text-align: center;
		color: #151515;
	}

	&__text {
		line-height: 150%;
		text-align: center;
		color: #151515;
		margin-top: 25rem;
	}

	&__form {
		max-width: 315rem;
		margin: 30rem auto 0;
	}

	&__item {
		margin-bottom: 35rem;
	}

	&__label {
		display: block;
		font-size: 14rem;
		line-height: 150%;
		color: #151515;
		margin-bottom: 15rem;
	}

	&__input {
		background: #F5F6FA;
		border: 1px solid #F5F6FA;
		border-radius: 10rem;
		height: 60rem;
		width: 100%;
		outline: none;
		padding: 0 25rem;
		font-size: 14rem;
		color: #7B7B7B;
	}

	&__btn {
		display: flex;
		align-items: center;
		justify-content: center;
		background: linear-gradient(180deg, #fdc52a 0, #d59d01 100%);
		border-radius: 10rem;
		font-weight: 500;
		font-size: 21rem;
		color: #fff;
		width: 100%;
		height: 80rem;
		transition: all 0.3s ease;

		&:hover{
			color: #eee;
		}
	}
}