.footer {
	background-color: #00497E;

	&__content {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 45rem 0;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: center;
		}
	}

	&__social {
		display: flex;

		@media (max-width: 991px) {
			margin: 40rem 0;
		}

		a+a {
			margin-left: 20rem;
		}
	}

	&__info {
		display: flex;
		position: relative;
		z-index: 3;

		@media (max-width: 991px){
			width: 100%;
			justify-content: center;
		}
	}

	&__box {
		margin-left: 15rem;
	}

	&__time {
		font-size: 12rem;
		color: #fff;
	}

	&__tel {
		display: block;
		font-weight: 700;
		font-size: 18rem;
		color: #fff;
		margin-top: 5rem;
		transition: all 0.3s ease;

		&:hover {
			color: #fdc52a;
		}
	}
}