.advantage {
	padding-top: 156rem;

	@media (max-width: 991px){
		padding-top: 70rem;
	}

	&__content {
		@media (min-width: 992px) {
			display: flex;
		}
	}

	&__item {
		position: relative;

		img {
			@media (max-width: 991px) {
				width: 100%;
			}
		}
	}

	&__text {
		position: absolute;
		top: 50rem;
		left: 50rem;
		max-width: 450rem;
	}

	&__title {
		font-weight: 600;
		font-size: 40rem;
		line-height: 47rem;
		color: #fff;
	}

	&__desc {
		font-size: 18rem;
		line-height: 26rem;
		color: #fff;
		margin-top: 15rem;
	}

	&__box {
		@media (min-width: 992px) {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			margin-left: 20rem;
		}
	}
}

.container {}