.partners {
	position: relative;
	background-color: #00497E;
	padding-top: 185rem;
	padding-bottom: 100rem;
	margin-bottom: 400rem;

	&::before {
		content: '';
		background-image: url('../img/waves-2.png');
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		position: absolute;
		bottom: -342rem;
		left: 0;
		width: 100%;
		height: 342rem;
	}

	&__title {
		font-weight: 600;
		font-size: 58rem;
		text-align: center;
		letter-spacing: 0.04em;
		color: #fff;
	}

	&__swiper {
		margin-top: 70rem;
	}

	&__img {
		width: 140rem;
		height: 140rem;

		img{
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.swiper-slide{
		display: flex;
		justify-content: center;
	}
}