.map {
	position: relative;
	margin-top: 335rem;

	@media (max-width: 991px){
		margin: 70rem 0 100rem;
	}

	&__form {
		@media (min-width: 992px) {
			position: absolute;
			top: -65rem;
			left: 370rem;
		}

		.popup__content {
			transform: none;
		}
	}

	&__address {
		position: absolute;
		background-color: #fff;
		padding: 20rem;
		border-radius: 10rem;

		@media (max-width: 991px) {
			padding: 15rem;
		}

		&::before {
			@media (min-width: 992px) {
				content: '';
				display: block;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 30px 25px 0 25px;
				border-color: #ffffff transparent transparent transparent;
				position: absolute;
				top: 108rem;
				left: 30rem;
			}
		}

		&--1 {
			top: 128rem;
			left: 1079rem;

			@media (max-width: 991px) {
				top: -14rem;
				left: 263rem;
			}

			.map__loc {
				color: #17CDC9;
			}
		}

		&--2 {
			top: 407rem;
			left: 1214rem;

			@media (max-width: 991px) {
				top: 102rem;
				left: 303rem;
			}

			.map__loc {
				color: #3D60FE;
			}
		}
	}

	&__subtitle {
		font-weight: 500;
		font-size: 21rem;
		color: #151515;
	}

	&__loc {
		font-weight: 500;
		margin: 10rem 0;
	}

	&__tel {
		font-weight: 500;
		color: #EE2B62;
	}
}