.price {
	margin-top: 230rem;

	@media (max-width: 991px) {
		margin-top: 70rem;
	}

	&__content {
		position: relative;
		background-image: url('../img/bg-catalog.png');
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		height: 634rem;
		max-width: 1364rem;
		margin: auto;
		padding: 60rem 90rem;

		@media (max-width: 991px) {
			margin-left: 15rem;
			margin-right: 15rem;
			height: 245rem;
			padding: 20rem;
		}
	}

	&__desc {
		font-weight: 500;
		font-size: 21rem;
		letter-spacing: 0.04em;
		text-transform: uppercase;
		color: #fff;

		@media (max-width: 991px) {
			font-size: 18rem;
		}
	}

	&__title {
		font-size: 67rem;
		line-height: 75rem;
		letter-spacing: 0.06em;
		color: #fff;
		margin-top: 15rem;
		max-width: 660rem;

		@media (max-width: 991px) {
			font-size: 32rem;
			line-height: 42rem;
		}
	}

	&__link {
		margin-top: 80rem;
		width: 378rem;
		height: 100rem;
		border-radius: 10rem;
		border: 3px solid #fff;
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		font-weight: 500;
		font-size: 21rem;
		color: #fff;

		@media (max-width: 991px) {
			margin-top: 40rem;
			height: 50rem;
			width: 278rem;
			font-size: 18rem;
			border-width: 1px;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/file.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 70rem;
			transform: translateY(-50%);
			width: 24rem;
			height: 24rem;
			transition: all 0.3s ease;

			@media (max-width: 991px){
				left: 20rem;
			}
		}

		&:hover:before {
			top: 54%;
		}
	}

	&__img {
		position: absolute;
		top: -25rem;
		right: -60rem;
		width: 720rem;
		height: 756rem;

		@media (max-width: 991px) {
			width: 140rem;
			height: 153rem;
			top: 95rem;
			right: 10rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}