.intro {
	min-height: 880rem;
	background-image: url('../img/intro.jpg');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	padding: 85rem 0 0 340rem;

	@media (max-width: 991px) {
		padding: 40rem 0 0;
	}

	.container {
		max-width: 1520rem;
	}

	&__content {
		position: relative;
		display: flex;

		@media (max-width: 991px) {
			display: block;
		}
	}

	&__left {
		@media (min-width: 992px) {
			flex: 0 0 45%;
		}

		@media (max-width: 991px) {
			margin-bottom: 60rem;
			display: flex;
			flex-direction: column;
			align-items: center;
			text-align: center;
		}
	}

	&__next {
		display: flex;
		flex-direction: column;
		position: absolute;
		width: 100%;
		top: 350rem;
		left: -80rem;

		@media (max-width: 991px) {
			display: none;
		}

		p {
			position: absolute;
			left: 0;
			transform: rotate(-90deg) translate(0, 0);
			transform-origin: left top;
			color: #818181;
			font-size: 18rem;
			line-height: 36rem;
		}
	}

	&__line {
		height: 42rem;
		width: 36rem;
		margin: 7rem 0;
	}

	&__mouse {
		width: 36rem;
		height: 36rem;
		animation: translateY 2.5s ease infinite;
	}

	@keyframes translateY {
		0% {
			transform: translateY(0)
		}

		50% {
			transform: translateY(15px)
		}
	}

	&__brand {
		display: inline-block;
		padding: 10rem 18rem;
		background: #2EC0BD;
		border-radius: 5rem;
		font-weight: 500;
		font-size: 11rem;
		line-height: 150%;
		color: #fff;
		text-transform: uppercase;
	}

	&__title {
		font-weight: 400;
		font-size: 58rem;
		line-height: 72rem;
		letter-spacing: 0.04em;
		color: #151515;
		margin-top: 22rem;
		min-height: 225rem;

		@media (max-width: 991px) {
			font-size: 28rem;
			line-height: 44rem;
			min-height: 133rem;
			max-width: 330rem;
		}

		i {
			font-style: normal;
		}
	}

	&__desc {
		font-size: 24rem;
		line-height: 143%;
		margin-top: 22rem;

		@media (max-width: 991px) {
			font-size: 18rem;
		}
	}

	&__btn {
		position: relative;
		display: inline-flex;
		align-items: center;
		margin-top: 50rem;
		background: linear-gradient(93.65deg, #FEC62B 0.39%, #D39B00 92.5%);
		box-shadow: 0px 12px 0px -6px #A07600;
		border-radius: 10px;
		padding: 40rem 90rem 40rem 60rem;
		font-weight: 500;
		font-size: 21rem;
		line-height: 20rem;
		color: #ffff;
		transition: all 0.3s ease;

		&:hover{
			color: #eee;
		}

		@media (max-width: 991px) {
			padding: 30rem 70rem 30rem 50rem;
			font-size: 18rem;
			margin-top: 30rem;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/list.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 38rem;
			right: 45rem;
			width: 27rem;
			height: 27rem;

			@media (max-width: 991px) {
				top: 28rem;
				right: 35rem;
			}
		}
	}

	&__img {
		width: 685rem;
		height: 777rem;
		padding-top: 20rem;

		@media (max-width: 991px) {
			width: 485rem;
			height: 547rem;
			padding-top: 0;
			margin: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}