.gallery {
	margin-top: 200rem;

	@media (max-width: 991px){
		margin-top: 70rem;
	}

	&__content {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-gap: 5rem;

		@media (max-width: 991px) {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
		}
	}

	&__img {
		&--1 {
			width: 636rem;
			height: 446rem;
		}

		&--2 {
			width: 636rem;
			height: 629rem;
		}

		&--3 {
			width: 636rem;
			height: 446rem;
		}

		&--4 {
			width: 636rem;
			height: 629rem;
			margin-top: -183rem;

			@media (max-width: 991px) {
				margin-top: 0;
			}
		}

		&--5 {
			width: 636rem;
			height: 446rem;
		}

		&--6 {
			width: 636rem;
			height: 629rem;
			margin-top: -183rem;

			@media (max-width: 991px) {
				margin-top: 0;
			}
		}

		@media (max-width: 991px) {
			height: 300rem;
			flex: 0 0 48%;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}