.header {

	&__content {
		display: flex;
		align-items: center;
		padding: 35rem 0;

		@media (max-width: 991px) {
			justify-content: space-between;
			padding: 20rem 0;
		}
	}

	&__logo {
		width: 200rem;
		height: 41rem;
		position: relative;
		z-index: 3;

		@media (max-width: 991px) {
			width: 120rem;
			height: 30rem;
		}
	}

	&__lang {
		margin-left: 80rem;

		@media (max-width: 991px) {
			margin: 0 0 30rem 0;
		}
	}

	&__info {
		display: flex;
		margin-left: 45rem;
		position: relative;
		z-index: 3;

		@media (max-width: 991px) {
			margin-left: auto;
			margin-right: 20rem;
		}

		img {
			width: 36rem;
			height: 36rem;
		}
	}

	&__box {
		margin-left: 15rem;
	}

	&__tel {
		font-weight: 700;
		font-size: 18rem;
		line-height: 152%;
		color: #11263A;
		transition: all 0.3s ease;

		&:hover {
			color: $color-primary;
		}

		@media (max-width: 991px) {
			font-size: 14rem;
		}
	}

	&__time {
		font-size: 12rem;
		line-height: 134.09%;
	}
}

.socials {
	@media (min-width: 992px) {
		margin-left: auto;
	}

	&__link+&__link {
		margin-left: 10rem;
	}

	&__link {
		img {
			width: 36rem;
			height: 36rem;
		}
	}
}

.menu {
	@media (min-width: 992px) {
		padding: 30rem 0;
		border-top: 1px solid #E2EAF2;
	}

	&__body {
		@media (max-width: 991px) {
			background-color: #fff;
			position: fixed;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 90rem 20rem 20rem;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;
			z-index: 2;

			&:before {
				content: "";
				background-color: #fff;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 70rem;
				z-index: 50;
			}

			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		&.active {
			@media (max-width: 991px) {
				.menu__list {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}

	&__list {
		display: flex;
		justify-content: center;

		@media (max-width: 991px) {
			flex-direction: column;
			margin-bottom: 30rem;
		}

		&>li {
			@media (max-width: 991px) {
				border-top: 1rem solid #F2F2F2;

				&:last-child {
					border-bottom: 1rem solid #F2F2F2;
				}
			}

			&>a {
				position: relative;
				font-size: 18rem;
				font-weight: 500;
				line-height: 20rem;
				color: #11263A;

				@media (min-width: 992px) {
					padding-bottom: 5rem;
					transition: color .1s ease-in;

					&::before {
						content: "";
						position: absolute;
						left: 51%;
						right: 51%;
						bottom: 0;
						background: #FDC52A;
						height: 2rem;
						transition-property: left, right;
						transition-duration: .3s;
						transition-timing-function: ease-out;
					}

					&:hover:before {
						left: 0;
						right: 0;
					}

					&:hover {
						color: #FDC52A;
					}
				}

				@media (max-width: 991px) {
					padding: 20rem 0;
					display: block;
				}
			}
		}

		&>li+li {
			margin-left: 85rem;

			@media (max-width: 991px) {
				margin-left: 0;
			}
		}
	}
}

@media (max-width: 991px) {
	.menu__icon {
		z-index: 5;
		display: block;
		position: relative;
		width: 35rem;
		height: 20rem;
		cursor: pointer;
	}

	.menu__icon span,
	.menu__icon::before,
	.menu__icon::after {
		left: 0;
		position: absolute;
		height: 10%;
		width: 100%;
		transition: all 0.3s ease 0s;
		background-color: $color-primary;
	}

	.menu__icon::before,
	.menu__icon::after {
		content: "";
	}

	.menu__icon::before {
		top: 0;
	}

	.menu__icon::after {
		bottom: 0;
	}

	.menu__icon span {
		top: 50%;
		transform: scale(1) translate(0rem, -50%);
	}

	.menu__icon.active span {
		transform: scale(0) translate(0rem, -50%);
	}

	.menu__icon.active::before {
		top: 50%;
		transform: rotate(-45deg) translate(0rem, -50%);
	}

	.menu__icon.active::after {
		bottom: 50%;
		transform: rotate(45deg) translate(0rem, 50%);
	}

	.menu__icon i {
		content: "";
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: -1;
		width: 50rem;
		height: 50rem;
		background: transparent;
		border-radius: 7rem;
		opacity: 0.5;
		transition: 0.3s ease-out;
	}

