.box {
	@media (min-width: 992px) {
		margin-top: -200rem;
		position: relative;
		z-index: 1;
	}

	@media (max-width: 991px) {
		margin-top: 30rem;
	}

	&__content {
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px){
			flex-wrap: wrap;
		}
	}

	&__item {
		display: flex;
		align-items: center;
		flex: 0 0 260rem;
		height: 92rem;

		@media (max-width: 991px) {
			flex: 0 0 48%;
			margin: 5rem;
		}

		&::before {
			content: '';
			background-image: url('../img/frame.png');
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			position: absolute;
			width: 262rem;
			height: 92rem;
			z-index: -1;
		}

		img {
			padding: 0 0 10rem 7rem;
			width: 90rem;
			height: 90rem;
		}

		p {
			font-weight: 500;
			line-height: 23rem;
			color: #06090D;
			margin-left: 20rem;
		}

		span {
			font-weight: 600;
		}
	}
}