.faq {
	position: relative;
	padding-top: 265rem;

	@media (max-width: 991px){
		padding-top: 70rem;
	}

	&__title {
		font-weight: 600;
		font-size: 52rem;
		text-align: center;
		color: #151515;
	}

	&__content {
		margin-top: 70rem;

		@media (max-width: 991px){
			margin-top: 40rem;
		}
	}

	&__item {
		border-radius: 8rem;
	}

	&__item+&__item {
		margin-top: 10rem;
	}

	&__subtitle {
		background: #F5F6FA;
		font-weight: 500;
		font-size: 21rem;
		line-height: 143%;
		color: #151515;
		padding: 40rem;
		cursor: pointer;
		border-radius: 8rem;
		transition: all 0.3s ease;

		&:hover{
			color: #fdc52a;
		}

		&.active {
			border-radius: 8rem 8rem 0 0;
			color: #fdc52a;
		}
	}

	&__body {
		display: none;
		padding: 0 40rem 40rem 40rem;
		background: #F5F6FA;
		border-radius: 0 0 8rem 8rem;
		line-height: 150%;
		color: #151515;

		p+p {
			margin-top: 10rem;
		}
	}

	&__img {
		position: absolute;

		@media (max-width: 991px){
			display: none;
		}

		&--1 {
			top: 120rem;
			left: 201rem;
		}

		&--2 {
			top: 472rem;
			left: 0;
		}

		&--3 {
			top: 1080rem;
			left: 0;
		}

		&--4 {
			top: 75rem;
			right: 0;
		}

		&--5 {
			top: 707rem;
			right: 0;
		}
	}
}