.examination {
	margin-top: 135rem;

	@media (max-width: 991px) {
		margin-top: 70rem;
	}

	&__title {
		font-weight: 600;
		font-size: 36rem;
		text-align: center;
		color: #151515;
	}

	&__content {
		margin-top: 80rem;

		@media (min-width: 992px) {
			display: flex;
			justify-content: space-between;
		}

		@media (max-width: 991px) {
			margin-top: 40rem;
		}
	}

	&__item {
		position: relative;
		box-shadow: 0px 0px 15px rgba(180, 180, 180, 0.11);
		border-radius: 10rem;
		padding: 50rem;

		@media (min-width: 992px) {
			flex: 0 0 32%;
		}
	}

	&__item+&__item {
		@media (max-width: 991px) {
			margin-top: 20rem;
		}
	}

	&__num {
		position: absolute;
		right: 100rem;
		font-weight: 600;
		font-size: 21rem;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		color: #fff;
		width: 52rem;
		height: 52rem;
		border-radius: 50%;

		@media (max-width: 991px) {
			right: 170rem;
		}

		&--1 {
			background-color: #FEC62B;
		}

		&--2 {
			background-color: #EE2B62;
		}

		&--3 {
			background-color: #17CDC9;
		}
	}

	&__img {
		width: 216rem;
		height: 216rem;
		border-radius: 50%;
		margin: auto;

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__subtitle {
		font-weight: 600;
		font-size: 21rem;
		line-height: 143%;
		text-align: center;
		color: #151515;
		margin-top: 25rem;
	}

	&__desc {
		line-height: 150%;
		text-align: center;
		color: #151515;
		margin-top: 10rem;
	}
}