.catalog {
	padding-top: 165rem;

	@media (max-width: 991px) {
		padding-top: 70rem;
	}

	&__title {
		font-weight: 600;
		font-size: 58rem;
		line-height: 72rem;
		text-align: center;
		letter-spacing: 0.04em;
		color: #151515;
	}

	&__tabs {
		margin-top: 60rem;

		@media (max-width: 991px) {
			margin-top: 30rem;
		}
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 40rem 20rem;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 250rem);
		}
	}

	&__item {
		box-shadow: 0px 8px 18px -6px rgba(24, 39, 75, 0.12), 0px 12px 42px -4px rgba(24, 39, 75, 0.12);
		border-radius: 8px;
		border: 3px solid transparent;

		&:hover {
			border-color: #FEC62B;

			@media (min-width: 992px) {
				.catalog__subtitle {
					color: #FEC62B;
				}

				.catalog__box {
					display: none;
				}

				.catalog__order {
					display: flex;
					align-items: center;
					justify-content: center;
				}
			}
		}
	}

	&__header {
		position: relative;
		padding-top: 40rem;
	}

	&__category {
		position: absolute;
		top: 20rem;
		left: 31rem;
		width: 51rem;
		height: 51rem;
		background-color: #F5F6FA;
		border-radius: 50%;

		@media (max-width: 991px) {
			top: 10rem;
			left: 10rem;
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 30rem;
			height: 30rem;
		}

		&--bestsellers {
			&::before {
				background-image: url('../img/svg/fire.svg');
			}
		}

		&--boys {
			&::before {
				background-image: url('../img/svg/boys.svg');
			}
		}

		&--girls {
			&::before {
				background-image: url('../img/svg/girls.svg');
			}
		}
	}

	&__img {
		width: 211rem;
		height: 290rem;
		margin: auto;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__body {
		background-color: #F5F6FA;
		padding: 20rem 40rem;

		@media (max-width: 991px) {
			padding: 20rem;
		}
	}

	&__subtitle {
		font-weight: 600;
		font-size: 34rem;
		letter-spacing: 0.04em;
		color: #151515;
		margin-bottom: 20rem;
		transition: all 0.3s ease;

		@media (max-width: 991px) {
			font-size: 24rem;
		}
	}

	&__box {
		display: flex;
	}

	&__box+&__box {
		margin-top: 10rem;
	}

	&__label {
		font-weight: 500;
		font-size: 24rem;
		color: #4D4D4D;
		margin-right: 10rem;

		@media (max-width: 991px) {
			font-size: 18rem;
		}
	}

	&__value {
		position: relative;
		font-size: 24rem;

		@media (max-width: 991px) {
			font-size: 18rem;
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 0px;
			left: 0px;
			width: 31rem;
			height: 31rem;

			@media (max-width: 991px) {
				top: 50%;
				transform: translateY(-50%);
			}
		}

		&--textile {
			color: #17CDC9;
		}

		&--girls {
			color: #EA5C84;
			padding-left: 37rem;

			&::before {
				background-image: url('../img/svg/girls.svg');
			}
		}

		&--boys {
			color: #2B48D3;
			padding-left: 37rem;

			&::before {
				background-image: url('../img/svg/boys.svg');
			}
		}
	}

	&__order {
		position: relative;
		display: none;
		background: linear-gradient(180deg, #fdc52a 0, #d59d01 100%);
		border-radius: 10rem;
		font-weight: 500;
		font-size: 21rem;
		color: #fff;
		width: 100%;
		height: 65rem;
		transition: all 0.3s ease;

		&::before {
			content: '';
			background-image: url('../img/svg/bag.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 60rem;
			transform: translateY(-50%);
			width: 24rem;
			height: 24rem;
		}
	}

	&__more {
		text-align: center;
		margin-top: 80rem;

		a {
			position: relative;
			display: inline-block;
			background: #F2F6FA;
			color: #11263A;
			border-radius: 5rem;
			padding: 18rem 25rem 18rem 55rem;
			font-weight: 500;
			font-size: 18rem;
			transition: all 0.3s ease;

			&:hover {
				background: #e9f2fb;
			}

			&::before {
				content: '';
				background-image: url('../img/svg/refresh.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 50%;
				left: 20rem;
				transform: translateY(-50%);
				width: 20rem;
				height: 20rem;
			}
		}
	}
}