.about-us {
	position: relative;
	margin-top: 500rem;
	background-color: #00497E;

	@media (max-width: 991px) {
		margin-top: 200rem;
	}

	&::before {
		content: '';
		background-image: url('../img/waves-1.png');
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		position: absolute;
		top: -376rem;
		left: 0;
		width: 100%;
		height: 376rem;

		@media (max-width: 991px) {
			top: -115rem;
			height: 115rem;
		}
	}

	&__logo {
		width: 612rem;
		height: 126rem;
		margin: auto;

		@media (max-width: 991px){
			width: 350rem;
			height: 72rem;
		}

		img{
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__content {
		margin-top: 210rem;

		@media (max-width: 991px) {
			margin-top: 100rem;
		}
	}

	&__item {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__item+&__item {
		margin-top: 80rem;
	}

	&__left {

		&--1 {
			@media (min-width: 992px) {
				margin-right: 25rem;
				flex: 0 0 52%;
			}
		}

		&--2 {
			@media (min-width: 992px) {
				margin-left: 56rem;
				flex: 0 0 46%;
			}

			@media (max-width: 991px) {
				order: -1;
			}
		}
	}

	&__title {
		font-weight: 600;
		font-size: 36rem;
		line-height: 133%;
		color: #fff;
	}

	&__desc {
		font-size: 21rem;
		line-height: 143%;
		color: #E6E9F5;
		margin-top: 20rem;
	}

	&__date {
		font-weight: 500;
		font-size: 16rem;
		line-height: 26rem;
		color: #FFE500;
		margin-top: 30rem;
		font-style: italic;

		i {
			display: block;
			font-weight: 500;
			font-size: 18rem;
			line-height: 20rem;
			color: #fff;
		}

		span {
			font-weight: 500;
			font-size: 52rem;
			line-height: 65rem;
			color: #FFE500;
			margin-right: 10rem;
		}
	}

	&__img {
		width: 100%;
		max-width: 580rem;
		height: 380rem;

		@media (max-width: 991px) {
			margin-top: 20rem;
			height: 300rem;
		}

		img {
			border-radius: 10rem;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__boxes {
		margin-top: 40rem;
		display: flex;
	}

	&__box {
		position: relative;
		font-weight: 500;
		font-size: 28rem;
		font-style: italic;
		line-height: 65rem;
		color: #FFE500;

		&::before {
			position: absolute;
			top: -15rem;
			left: -25rem;
			opacity: 0.3;
			color: #fff;
			font-size: 52rem;

			@media (max-width: 991px) {
				left: 0;
			}
		}

		&--1 {
			&::before {
				content: '01';
			}
		}

		&--2 {
			&::before {
				content: '02';
			}
		}

		&--3 {
			&::before {
				content: '03';
			}
		}
	}

	&__box+&__box {
		margin-left: 40rem;
	}

	&__countries {
		margin-top: 45rem;
		display: flex;
		flex-wrap: wrap;
	}

	&__country {
		font-style: italic;
		font-weight: 500;
		font-size: 21rem;
		line-height: 143%;
		color: #FFE600;
		flex: 0 0 50%;
		padding-bottom: 15rem;
	}
}