html {
	box-sizing: border-box;
	font-size: 0.052083333333333336vw;

	@media (max-width: 991px) {
		font-size: 0.18vw;
	}
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	min-width: 320rem;
	font-family: "Gilroy", sans-serif;
	color: #11263A;
	font-size: 16rem;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

$color-primary: #FDC52A;

.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.main {
	flex: 1 1 auto;
}

.container {
	margin: 0 auto;
	padding: 0 15rem;
	max-width: 1180rem;

	@media (max-width: 767px) {
		max-width: none;
	}
}

.title {
	font-weight: 600;
	font-size: 52rem;
	line-height: 65rem;
}

.btn {
	position: relative;
	overflow: hidden;

	&__animate {
		height: 250px;
		width: 250px;
		position: absolute;
		top: -20px;
		left: -120px;
		background: #fff;
		border-radius: 100%;
		transform: scale(0);
		animation: ripple 4s linear infinite;
	}
}

@keyframes ripple {
	20% {
		opacity: 0;
		transform: scale(2.5);
	}

	100% {
		opacity: 0;
		transform: scale(2.5);
	}
}

#up {
	display: none;
	position: fixed;
	bottom: 30rem;
	right: 30rem;
	cursor: pointer;
	z-index: 1;
	opacity: 0.5;
	transition: all 0.3s ease;

	@media (max-width: 575rem) {
		bottom: 10rem;
		right: 10rem;
	}

	&:hover {
		opacity: 1;
	}
}