.form {
	box-shadow: 0px 8px 18px -6px rgba(24, 39, 75, 0.12),
		0px 12px 42px -4px rgba(24, 39, 75, 0.12);
	background-color: #fff;
	border-radius: 20rem;
	max-width: 1024rem;
	margin: auto;
	padding: 60rem 80rem;
	margin-top: 35rem;
	border-top: 14px solid #2EC0BD;
	position: relative;
	z-index: 1;

	@media (max-width: 991px) {
		margin-left: 15px;
		margin-right: 15px;
		padding: 40rem;
	}

	&__title {
		font-size: 36rem;
		line-height: 133%;
		text-align: center;
		color: #151515;
	}

	&__desc {
		font-size: 21rem;
		line-height: 143%;
		margin-top: 20rem;
		text-align: center;
	}

	&__items {
		margin-top: 35rem;

		@media (min-width: 992px) {
			display: flex;
			justify-content: space-between;
		}
	}

	&__item {
		position: relative;

		@media (max-width: 991px) {
			margin-bottom: 20rem;
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 25rem;
			transform: translateY(-50%);
		}

		&--email {
			&::before {
				background-image: url('../img/svg/pensil.svg');
				width: 16rem;
				height: 16rem;
			}
		}

		&--tel {
			&::before {
				background-image: url('../img/svg/tel-2.svg');
				width: 14rem;
				height: 14rem;
			}
		}
	}

	&__input {
		background: #F5F6FA;
		border-radius: 10rem;
		font-size: 15rem;
		line-height: 160%;
		color: #7B7B7B;
		width: 240rem;
		height: 66rem;
		padding: 0 50rem;
		outline: none;

		@media (max-width: 991px) {
			width: 100%;
		}
	}

	&__btn {
		background: linear-gradient(180deg, #FDC52A 0%, #D59D01 100%);
		border-radius: 10rem;
		font-weight: 600;
		line-height: 152%;
		color: #fff;
		width: 328rem;
		height: 66rem;
		transition: all 0.3s ease;

		&:hover{
			color: #eee;
		}

		@media (max-width: 991px) {
			width: 100%;
		}
	}
}